<template>
  <div class="swap-box invest-box">
    <HeadTop title="质押记录" :isShowTop="true" :isSubHeader="false"></HeadTop>
    <van-list v-model:loading="loading" :immediate-check="false" v-model:error="error" error-text="请求失败，点击重新加载" :finished="finished" finished-text="没有更多了" @load="onLoad">
    <div class="list-box">
       <div class="item" v-for="(item, index) in list" :key="index">
          <div class="top flex-middle">
            <div class="top-item flex-item">
               <div class="num">{{item.stakeKttAmount}}</div>
               <div class="desc">质押KTT</div>
            </div>
            <div class="line"></div>
            <div class="top-item flex-item">
               <div class="num yellow">{{item.totalRewardKtm}}</div>
               <div class="desc">KTM总收益</div>
            </div>
          </div>
          <div class="bottom">
             <div class="row flex-middle">
                <div class="name">时长</div>
                <div class="value">{{item.lockTime}}天</div>
             </div>
              <div class="row flex-middle">
                <div class="name">倍数</div>
                <div class="value">{{item.rate}}</div>
             </div>
             <div class="row flex-middle">
                <div class="name">已领取</div>
                <div class="value">{{item.haveBack}}KTM</div>
             </div>
             <div class="row flex-middle">
                <div class="name">待领取</div>
                <div class="value">{{item.canGetAmount}}KTM</div>
             </div>
             <div class="row flex-middle">
                <div class="name">质押时间</div>
                <div class="value">{{item.stakeTime}}</div>
             </div>
             <div class="row flex-middle">
                <div class="name">质押到期时间</div>
                <div class="value">{{item.endTime}}</div>
             </div>
          </div>
           <div class="btn-box flex-between">
               <span></span>
                <van-button type="primary" class="btn-green" style="width: 120px" block @click="withdrawRewardStakeRecord(item.id)">领取</van-button>
           </div>
       </div>
    </div>
    </van-list>
  </div>  
</template>
<script>
import HeadTop from '@/components/HeadTop'
import { reactive, toRefs, computed, watch } from "vue";
import { useStore } from "vuex";
import swapAbi from "@/utils/swapAbi";
import { Toast } from 'vant';
import config from "@/utils/config";
import BigNumber from 'bignumber.js';
import dateformat from "dateformat"
export default {
  components: {
    HeadTop
  },
  setup () {
    const store = useStore()
    const data = reactive({
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      swapContract: null,
      page: 1,
      limit: 5,
      loading: false,
      error: false,
      finished: false,
      list: [],
      initContract: () => {
        data.swapContract = new data.web3.eth.Contract(swapAbi, config.swapAddress)
        data.initData()
      },
      initData: () => {
         data.list = []
        data.page = 1
        data.getList()
      },
      onLoad () {
        data.page++
        data.getList()
      },
      getList: async () => {
        let info = await data.swapContract.methods.getUserRecords(data.address,1,5).call({from: data.address})
        let periodTime = await data.swapContract.methods.periodTime().call()
        let timestamp = await data.swapContract.methods.getBlockTimestamp().call()
        let list = info.records
        console.log(list)
        let arr = []
         list.forEach((item) => {
          if(Number(item.id) > 0) {
            arr.push({
              stakeKttAmount:  new BigNumber(item.stakeKttAmount).dividedBy(10**18).toFixed(2),
              totalRewardKtm:  new BigNumber(item.totalRewardKtm).dividedBy(10**18).toFixed(2),
              dayBackKtm: new BigNumber(item.dayBackKtm).dividedBy(10**18).toFixed(2),
              id:item.id,
              rate: item.rate / 10000,
              lockTime: new BigNumber(item.period).dividedBy(periodTime).toFixed(1),
              stakeTime: dateformat(item.stakeTime*1000, 'yyyy-mm-dd HH:MM:ss'),
              endTime: dateformat((Number(item.stakeTime) + Number(item.period))*1000, 'yyyy-mm-dd HH:MM:ss'),
              haveBack: new BigNumber(item.haveBack).dividedBy(10**18).toFixed(2),
              canGetAmount: timestamp >= Number(item.stakeTime) + Number(item.period) ? 
              new BigNumber(parseInt(item.period / periodTime)).multipliedBy(item.dayBackKtm).minus(item.haveBack).dividedBy(10**18).toFixed(2) :
              new BigNumber(parseInt((timestamp - item.stakeTime) / periodTime)).multipliedBy(item.dayBackKtm).minus(item.haveBack).dividedBy(10**18).toFixed(2)
            })
          }
        })
         if (data.page === 1) {
            data.list = arr
          } else {
            data.list.push(...arr)
          }
          data.loading = false
          data.finished = !(Math.ceil(info.total / data.limit) > data.page)
      },
      withdrawRewardStakeRecord: async (id) => {
        try {
          await data.swapContract.methods.withdrawStakeRecord(id).send({from: data.address})
          Toast('领取成功')
        } catch (error) {
          Toast('领取失败')  
        }
        
      } 
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.invest-box{
  padding: 10px 16px 25px;
  .list-box{
    padding-top: 20px;
    .item{
      background: #3F2E6F;
    box-shadow: 0px 4px 0px 0px #3F2E6F;
    border-radius: 15px;
    padding: 25px 20px;
    margin-bottom: 20px;
      .top{
          padding-bottom: 20px;
          border-bottom: 0.5px solid rgba(255, 255, 255, 0.1);
        .top-item{
          .num{
            color: #fff;
            font-size: 18px;
            font-weight: 700;
            margin-bottom: 4px;
          }
          .yellow{
            color: #FFD511;
          }
          .desc{
            color: rgba(255, 255, 255, 0.7);
            font-size: 14px;
          }
        }
        .line{
          width: 0.5px;
          height: 40px;
          background: rgba(255, 255, 255, 0.1);
          margin-right: 18px;
        }
      }
      .bottom{
        padding: 18px 0 0;
        .status{
          color: #0CD3DD;
          font-size: 16px;
          font-weight: 700;
          padding-bottom: 4px;
        }
        .red{
          color: #F3489D;
        }
        .row{
          margin-top: 10px;
          .name{
            width: 110px;
            color: rgba(255, 255, 255, 0.7);
            font-size: 14px;
          }
          .value{
            color: #fff;
            font-size: 14px;
          }
        }
      }
      .btn-box{
        margin-top: 16px;
      }
    }
  }
}
</style>
